.App {
    text-align: center;
  }
  
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
    height: 40vmin;
    pointer-events: none;
  }
  
  .App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  
  .App-link {
    color: #61dafb;
  }
  
  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  
  .container-layer2 {
    background-color: #f8f9fa!important;
    margin-top: 12px;
    margin-bottom: 12px;
    min-height: 90vh;
  }
  
  .container-layer3 {
    background-color: white;
    min-height: 75vh;
  }
  
  .footer {
    /* position: fixed; */
    left: 0;
    bottom: 0;
    /* height: 4%; */
    width: 100%;
    /* background-color:#343a40!important; */
    color: black;
    text-align: center;
  }
  
  .header {
    position: fixed;
    height: 0;
    left: 0;
    bottom: 0;
  }